@media screen and (max-width: 767px) {
  .modal-form {
    flex-direction: column;
    display: flex;
  }

  .modal-form-title {
    text-align: center;
    letter-spacing: .03em;
    color: #212121;
    font-size: 20px;
    font-weight: 700;
    line-height: 1.5;
  }

  .modal-form-field {
    margin-bottom: 10px;
  }

  .modal-form-input {
    width: 100%;
    height: 40px;
    transition: border-color var(--transition-main);
    border: 1px solid rgba(33, 33, 33, .2);
    border-radius: 4px;
    padding-top: 16px;
    padding-bottom: 16px;
    padding-left: 42px;
  }

  .modal-form-input:focus {
    border-color: var(--accent-color);
    cursor: pointer;
    outline: none;
  }

  .modal-form-input-description {
    letter-spacing: .01em;
    color: #757575;
    margin-bottom: 4px;
    font-size: 12px;
    font-weight: 400;
    line-height: 1.17;
    display: block;
  }

  .modal-form-input-box {
    position: relative;
  }

  .modal-field-icon {
    transition: fill var(--transition-main);
    position: absolute;
    top: 50%;
    left: 12px;
    transform: translateY(-50%);
  }

  .modal-form-input:focus + .modal-form-input-icon {
    fill: var(--accent-color);
  }

  .modal-form-comment {
    width: 100%;
    height: 120px;
    resize: none;
    transition: border-color var(--transition-main);
    border: 1px solid rgba(33, 33, 33, .2);
    border-radius: 4px;
    padding: 12px 16px;
  }

  .modal-form-comment::-ms-input-placeholder {
    letter-spacing: .01em;
    color: rgba(117, 117, 117, .5);
    font-size: 12px;
    font-weight: 400;
    line-height: 14px;
  }

  .modal-form-comment::placeholder {
    letter-spacing: .01em;
    color: rgba(117, 117, 117, .5);
    font-size: 12px;
    font-weight: 400;
    line-height: 14px;
  }

  .modal-form-comment:focus {
    border-color: var(--accent-color);
    cursor: pointer;
    outline: none;
  }

  .policy-check-field {
    justify-content: center;
    align-items: center;
    margin-top: 6px;
    margin-bottom: 30px;
    display: flex;
  }

  .check-box-icon {
    width: 16px;
    height: 15px;
    transition: background-color var(--transition-main);
    border: 2px solid #212121;
    border-radius: 4px;
    margin-right: 8px;
    display: inline-block;
    position: relative;
  }

  .check-mark {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }

  .check-box-description {
    letter-spacing: .03em;
    color: #757575;
    font-size: 12px;
    font-weight: 400;
    line-height: 1.71;
  }

  .policy-link {
    color: #2196f3;
  }

  .check-box:checked + .check-box-icon {
    background-color: var(--accent-color);
    border-color: rgba(0, 0, 0, 0);
  }

  .check-box {
    -webkit-appearance: none;
    -ms-appearance: none;
    appearance: none;
  }

  .modal-form-submit {
    min-width: 200px;
    height: 50px;
    color: #fff;
    text-align: center;
    letter-spacing: .06em;
    transition: backgroud-color var(--transition-main);
    background: #2196f3;
    border: none;
    border-radius: 4px;
    align-self: center;
    padding: 10px 52px;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.87;
    display: flex;
    box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
  }
}

