.container {

    padding-left: 20px;
    padding-right: 20px;
}

.card-set {
    display: flex;
    flex-direction: column;
    gap: 20px;


    &__card {

        padding: 40px 32px;
        background: #FCFCFC;
        box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
        border-radius: 24px;
    }
}



.address {
    border-bottom: 1px solid #E1E1E1;
}

.address__type {
    display: flex;
    align-items: center;
    text-align: center;

    background: var(--main-button-color);
    border-radius: 4px;
    width: max-content;
    height: 16px;
    padding-left: 22.5px;
    padding-right: 22.5px;
    margin-bottom: 20px;

    font-family: var(--main-font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;


    letter-spacing: 0.04em;

    color: var(--franchise-button-color);
}

.address__city {
    margin-bottom: 8px;
    font-family: var(--main-font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 186%;
    letter-spacing: 0.04em;
    color: var(--black);
}

.address__street {
    margin-bottom: 24px;

    font-family: var(--main-font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 183%;
    letter-spacing: 0.04em;
    color: #D6936D;

    &:hover,
    &:focus {
        text-decoration-line: underline;
        cursor: url("../images/cursor.png"), auto;
    }
}

.working-mode {
    border-bottom: 1px solid #E1E1E1;
    padding-top: 24px;
    padding-bottom: 24px;

    &__days {
        font-family: var(--main-font-family);
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 186%;
        color: var(--black);

    }

    &__time {
        font-family: var(--main-font-family);
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 186%;
        color: var(--disable-button-color);

        &_marg {
            margin-bottom: 8px;
        }
    }
}

.mail-tel {
    padding-top: 24px;

    &__h {
        font-family: var(--main-font-family);
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 1.5;
        color: #131313;
    }

}

.contacts-button {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 32px;
}


.location {
    width: 196px;
    height: 42px;

    margin-bottom: 8px;
    background: var(--main-button-color);
    border-color: var(--main-button-color);
    border-radius: 10px;
    border: none;
    font-family: var(--main-font-family);
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.29;
    color: var(--secondary-black-color);

    &:hover {
        background-color: #0A8817;
        border-color: #0A8817;
    }

    &:focus {
        color: var(--franchise-button-color);
    }
}

.contacts-button-svg {
    fill: var(--secondary-black-color);
}

.location:focus .contacts-button-svg {
    fill: var(--franchise-button-color);
}


.button-inscrip {
    display: flex;
    justify-content: center;
    gap: 6px;
}


.Franchise {
    width: 196px;
    height: 42px;

    border: 2px solid var(--main-button-color);
    border-radius: 10px;

    font-family: var(--main-font-family);
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.29;
    color: var(--secondary-black-color);
    background-color: transparent;


    &:hover {
        border-color: #0A8817;
    }

    &:focus {
        color: var(--franchise-button-color);
    }
}

.Franchise:focus .contacts-button-svg {
    fill: var(--franchise-button-color);
}

