@media screen and (min-width: 768px) {

    .container {
        width: 768px;
        padding-left: 32px;
        padding-right: 32px;
    }

    .card-set {
        flex-direction: row;
        gap: 16px;

        &__card {
            width: 224px;
            padding: 40px 27px 40px 24px;
            background: var(--white);
            box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
            border-radius: 24px;
        }
    }

    .address__type {
        border-radius: 6px;
        height: 28px;
        padding-left: 13.5px;
        padding-right: 13.5px;
        margin-bottom: 32px;
        font-size: 16px;
        line-height: 186%;
        color: --secondary-black-color;
    }

    .address__city {
        margin-bottom: 12px;
        font-size: 14px;
    }

    .address__street {
        margin-bottom: 26px;
        font-size: 14px;
    }

    .working-mode {
        padding-top: 32px;
        padding-bottom: 32px;

        &__days {
            font-size: 14px;
            line-height: 186%;
        }

        &__time {
            font-size: 14px;
            color: #907E82;

            &_marg {
                margin-bottom: 12px;
            }
        }
    }

    .mail-tel {
        padding-top: 32px;

        &__h {
            font-size: 14px;
            line-height: 183%;
            color: var(--secondary-black-color);
        }
    }

    .contacts-button {
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 16px;
        align-items: baseline;
        margin-top: 64px;
    }

    .location {
        margin-bottom: 0px;
    }
}






@media screen and (min-width: 1200px) {
    .container {
        width: 1200px;
        padding-left: 16px;
        padding-right: 16px;
    }

    .card-set {
        gap: 32px;

        &__card {
            width: 368px;
            padding: 40px;
        }
    }

    .address__type {

        padding-left: 14.5px;
        padding-right: 14.5px;
    }

    .address__city {
        margin-bottom: 8px;
        font-size: 16px;
        color: var(--secondary-black-colo)
    }

    .address__street {
        margin-bottom: 32px;
        font-size: 16px;
    }

    .working-mode {

        &__days {
            font-size: 16px;
            color: var(--secondary-black-color);
        }

        &__time {
            font-size: 16px;
            color: var(--disable-button-color);
        }
    }

    .mail-tel {

        &__h {
            font-size: 16px;
        }
    }

}